/* „Ç∞„É©„Éá„Éº„Ç∑„Éß„É≥„ÇíËªΩÈáè„Å™„ÉÄ„Éº„ÇØ„Ç∞„É©„Éá„Éº„Ç∑„Éß„É≥„Å´Â§âÊõ¥ÔºàIntroduction.css„ÅßÂÆöÁæ©Ôºâ */
/* .performance-mode .intro-background „ÅÆ„Çπ„Çø„Ç§„É´„ÅØIntroduction.css„ÅßÁÆ°ÁêÜ */

/* üîß ‰øÆÊ≠£: highlight-text„ÅÆËÉåÊôØËâ≤ÂïèÈ°å„ÇíËß£Ê±∫ */
.performance-mode .highlight-text {
  background: rgba(168, 237, 234, 0.2) !important; /* ËñÑ„ÅÑÈùíÁ∑ëËâ≤„ÅÆËÉåÊôØ */
