  border: 1px solid rgba(168, 237, 234, 0.4) !important; /* ËñÑ„ÅÑÂ¢ÉÁïåÁ∑ö */
  backdrop-filter: none !important;
}

/* stat-item„ÅÆËÉåÊôØ„ÇÇ‰øÆÊ≠£ */
.performance-mode .stat-item {
  background: rgba(255, 255, 255, 0.15) !important;
