/* „É¢„Éº„ÉÄ„É´„Ç¢„Éã„É°„Éº„Ç∑„Éß„É≥„ÅÆÁ∞°Áï•Âåñ */
.performance-mode .modal-container {
  animation: simple-modal-fadeIn 0.1s ease !important;
}

@keyframes simple-modal-fadeIn {
