}

/* ‰Ωé„Çπ„Éö„ÉÉ„ÇØÁ´ØÊú´Âêë„Åë„ÅÆËøΩÂä†ÊúÄÈÅ©Âåñ */
@media screen and (max-width: 768px) {
  .performance-mode .intro-stats {
