}

/* ÈùûÂ∏∏„Å´‰Ωé„Çπ„Éö„ÉÉ„ÇØÁ´ØÊú´Âêë„ÅëÔºà4„Ç≥„Ç¢Êú™Ê∫Ä„ÄÅ4GBÊú™Ê∫ÄÔºâ */
@media screen and (max-width: 480px) {
