}

/* prefers-reduced-motion„Å®„ÅÆÁµ±Âêà */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-delay: 0s !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    transition-delay: 0s !important;
  }
  
  .floating-shapes,
  .gradient-orb,
  .floating-particles,
  .image-decoration,
  .shape {
    display: none !important;
  }
  
  .highlight-text {
    background: rgba(168, 237, 234, 0.2) !important;
    color: rgba(255, 255, 255, 0.95) !important;
    border: 1px solid rgba(168, 237, 234, 0.4) !important;
    backdrop-filter: none !important;
  }
  
  /* üîß prefers-reduced-motion„Åß„ÇÇIntroduction„Çª„ÇØ„Ç∑„Éß„É≥„ÅÆÁôΩ„ÅÑ„Ç™„Éº„Éê„Éº„É¨„Ç§„ÇíÂÆåÂÖ®Ëß£Ê±∫ */
  .content-body {
    background: transparent !important;
  }
  
  #research-section,
  #achievements-section,
  #education-section,
  #certifications-section,
  #contact-section {
    background-color: rgba(255, 255, 255, 0.9) !important;
    margin: 0 -15px !important;
    padding: 20px 15px !important;
    border-radius: 0 !important;
  }
  
  .section-divider {
    margin: 0 !important;
    background: rgba(0, 0, 0, 0.1) !important;
  }
}

/* „ÉÄ„Éº„ÇØ„É¢„Éº„ÉâÂØæÂøúÊôÇ„ÅÆ„Éë„Éï„Ç©„Éº„Éû„É≥„ÇπÊúÄÈÅ©Âåñ */
@media (prefers-color-scheme: dark) {
  .performance-mode {
    color-scheme: light; /* „ÉÄ„Éº„ÇØ„É¢„Éº„ÉâË®àÁÆó„ÇíÈÅø„Åë„Çã */
  }
}

/* Âç∞Âà∑ÊôÇ„ÅÆÊúÄÈÅ©Âåñ */
@media print {
  .performance-toggle-container,
  .floating-shapes,
  .gradient-orb,
  .floating-particles,
  .image-decoration,
